import React from "react";
import { useHistory } from "react-router-dom";
import { Result, Button } from "antd";
import AppFooter from "./../AppFooter";

const logo = {
  fontSize: "2em",
  padding: 0,
  margin: 0,
};

export const Error = (props) => {
  const history = useHistory();
  const irInicio = () => {
    history.push("/");
  };
  return (
    <div className="exception-body error">
      <div className="exception-topbar">
        <div id="logo-link" className="layout-topbar-logo">
          <span className="ck ck-bold ck-purple" style={logo}>
            ko
          </span>
          <span className="ck ck-bold ck-black" style={logo}>
            yn
          </span>
        </div>
      </div>
      <Result
        status="500"
        title="Ocurrio un error"
        subTitle="Lo sentimos, algo no sali bien."
        extra={
          <Button type="primary" onClick={irInicio}>
            Ir a Inicio
          </Button>
        }
      />
      <AppFooter />
    </div>
  );
};
