import React from 'react'
import PropTypes from 'prop-types'

import { Col, Row, Table, Typography } from 'antd'
import { formatoMoneda } from '../../utils/Utils'

import './TablaAmortizacion.css'

const columns = [
    {
        title: '#',
        dataIndex: 'numCuota'
    },
    {
        title: 'Fecha',
        dataIndex: 'fechaPago'
    },
    {
        title: 'Capital',
        dataIndex: 'amortizacion',
        render: (e) => (
            formatoMoneda(e)
        )
    }
    ,
    {
        title: 'Inters',
        dataIndex: 'interes',
        render: (e) => (
            formatoMoneda(e)
        )
    }
    ,
    {
        title: 'Recibes',
        dataIndex: 'cuotaMensual',
        className: 'aRecibir',
        render: (e) => (
            <Typography.Text strong>
                {formatoMoneda(e)}
            </Typography.Text>
        )
    }
]

const getTotal = (t) => {
    return t.reduce((p, c) => p + c.cuotaMensual, 0)
}

const TablaAmortizacion = (props) => {
    const { tabla, cuota, plazo, isLoading } = props
    return (
        <>
            <Row>
                <Col span={24}>
                    <Typography.Paragraph style={{ textAlign: 'center' }}>
                        <Typography.Title level={5} style={{ color: '#0E61D1', textTransform: 'uppercase' }}>
                            Por tu inversin de {formatoMoneda(cuota || 0)} recibiras en total {formatoMoneda(getTotal(tabla) || 0)} en {plazo} meses
                        </Typography.Title>
                    </Typography.Paragraph>
                </Col>
            </Row>
            {/* <Typography.Title level={5}>Detalle de Inversin</Typography.Title> */}
            <Table
                rowKey={'numCuota'}
                columns={columns}
                dataSource={tabla}
                size='small'
                pagination={{
                    defaultCurrent: 1,
                    total: tabla.length
                }}
                loading={isLoading}
            />
        </>
    )
}

TablaAmortizacion.propTypes = {
    tabla: PropTypes.arrayOf(Object).isRequired,
    cuota: PropTypes.number.isRequired,
    plazo: PropTypes.number.isRequired,
    isLoading: PropTypes.bool.isRequired
}

export default TablaAmortizacion