import AxiosInstance from "../utils/AxiosInstance";
import { getInversionesPorInversionistaUrl } from "../utils/urls";

export default class InversionService {
    getInversionesPorInversionista = (props) => {
        const { navigate, inversionista } = props
        return AxiosInstance(navigate).post(getInversionesPorInversionistaUrl(), inversionista)
    }
}