import React, { useEffect, useState } from 'react'
import { useHistory, useParams, Link } from 'react-router-dom'
import { Col, Row, Button, Result, Form, Input, Select, Spin, Typography, Divider, message } from 'antd'
import * as anticons from '@ant-design/icons'
import UsuarioService from '../../service/UsuarioService'
import CatalogoService from './../../service/CatalogoService'

import md5 from 'md5'

const usuarioService = new UsuarioService()
const catalogoService = new CatalogoService()
const { Option } = Select

const Usuario = () => {

  const navigate = useHistory()
  const { id } = useParams()
  const [isLoading, setIsLoading] = useState(true)
  const [isSaving, setIsSaving] = useState(false)
  const [estados, setEstados] = useState([])
  const [tiposIdentificacion, setTiposIdentificacion] = useState([])
  const [usuario, setUsuario] = useState({})
  const [error, setError] = useState(null)

  const [form] = Form.useForm()

  useEffect(() => {
    if (isLoading) {
      const fetchData = async () => {

        catalogoService.getPorPadre({ navigate, catalogo: { catNombre: 'ESTADOS' } }).then(r => {
          const { data } = r
          setEstados(data.listObject[0])
        })

        catalogoService.getTiposIdentificacion(navigate).then(r => {
          const { data } = r
          setTiposIdentificacion(data.listObject[0])
        })

        try {
          const { data } = await usuarioService.get({ navigate, usuario: { usuId: id } })
          if (data) {
            setUsuario({ ...data.listObject[0], password: '' })
            setError(null)
            setIsLoading(false)
          } else {
            setIsLoading(false)
            setError("Hubo un error al obtener el usuario")
          }
        } catch (error) {
          setIsLoading(false)
          const { response } = error
          setError(response.status === 409 ? response.data[0].messageText : "No pudimos hacer la solicitud para obtener el usuario")
        }
      }
      fetchData()
    }
  }, [isLoading, navigate, id])

  const onChange = (e) => {
    setUsuario({ ...usuario, [e.target.name]: e.target.type === 'checkbox' ? e.target.checked : e.target.value })
  }

  const onChangeEstado = (v, o) => {
    setUsuario({ ...usuario, blnEstado: { catId: v, catValor: o.children } })
  }

  const onChangeTipoIdentificacion = (v, o) => {
    setUsuario({ ...usuario, tipoDocumento: v })
  }

  const guardar = async (e) => {
    setError('')
    setIsSaving(true)
    try {
      const { data } = await usuarioService.set({ navigate, usuario: { ...usuario, password: md5(usuario.password) } })
      setIsSaving(false)
      message.success('Usuario guardado correctamente')
      const usuarioActualizado = data.listObject[0]
      setUsuario(usuarioActualizado)
    }
    catch (error) {
      setIsSaving(false)
      setError("No pudimos hacer la solicitud para guardar el usuario")
    }
  }

  const renderEstado = e => <Option key={e.catId} value={e.catId}>{e.catNombre}</Option>
  const renderCatalogoValor = e => <Option key={e.catValor} value={e.catValor}>{e.catNombre}</Option>

  if (error) {
    return (
      <Result
        status="warning"
        title={''}
        subTitle={''}
        extra={
          <>
            <h5>{error}</h5>
            <Link to={'/usuarios'}>Regresar al inicio</Link>
          </>
        }
      />
    )
  }

  return (
    <>
      <Row align='middle' gutter={10}>
        <Col>
          <Link to={'/usuarios'}><anticons.ArrowLeftOutlined /></Link>
        </Col>
        <Col>
          <Typography.Title
            level={4}
            style={{
              margin: 0,
            }}
          >
            Gestin de Usuario
          </Typography.Title>
        </Col>
      </Row>
      <Divider></Divider>
      {
        isLoading ?
          <div className="p-10 text-center">
            <Spin size="large" /><br />
            <Typography.Text>Espere un momento...</Typography.Text>
          </div> :
          <Form
            layout={'vertical'}
            form={form}
            initialValues={{
              layout: 'vertical',
            }}
          >
            <Row gutter={10}>
              <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                <Form.Item label="Identificacin">
                  <Input id="documento" name="documento" label='Identificacin' type='text' value={usuario.documento} onChange={onChange} />
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                <Form.Item label="Tipo de Identificacin">
                  <Select defaultValue={usuario.tipoDocumento} value={usuario.tipoDocumento} onChange={onChangeTipoIdentificacion}>
                    {
                      tiposIdentificacion && tiposIdentificacion.map(p => renderCatalogoValor(p))
                    }
                  </Select>
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={10}>
              <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                <Form.Item label="Nombres">
                  <Input id="nombres" name="nombres" label='Nombres' type='text' value={usuario.nombres} onChange={onChange} />
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                <Form.Item label="Apellidos">
                  <Input id="apellidos" name="apellidos" label='Apellidos' type='text' value={usuario.apellidos} onChange={onChange} />
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={10}>
              <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                <Form.Item label="Direccin">
                  <Input id="direccion" name="direccion" label='Direccin' type='text' value={usuario.direccion} onChange={onChange} />
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={10}>
              <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                <Form.Item label="Email">
                  <Input id="email" name="email" label='Email' type='text' value={usuario.email} onChange={onChange} />
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                <Form.Item label="Telfono">
                  <Input id="telefono" name="telefono" label='Telfono' type='text' value={usuario.telefono} onChange={onChange} />
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={10}>
              <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                <Form.Item label="Login">
                  <Input id="login" name="login" label='Login' type='text' value={usuario.login} onChange={onChange} disabled />
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                <Form.Item label="Contrasea">
                  <Input.Password id="password" name="password" label='Contrasea' value={usuario.password} onChange={onChange} />
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={10}>
             <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                <Form.Item label="Estado">
                  <Select
                    defaultValue={usuario.blnEstado.catValor}
                    value={usuario.blnEstado.catValor}
                    onChange={onChangeEstado}
                  >
                    {
                      estados && estados.map(e => renderEstado(e))
                    }
                  </Select>
                </Form.Item>
              </Col>
            </Row>
            <Form.Item>
              <Button type="primary" onClick={(e) => guardar(e)} disabled={isSaving}
                loading={isSaving}
              >
                {isSaving ? 'Guardando...' : 'Guardar'}
              </Button>
            </Form.Item>
          </Form>
      }
    </>
  )
}

export default Usuario