import React from 'react'
import PropTypes from 'prop-types'
import { Row, Col, Typography } from 'antd'

const Titulo = (props) => {
    const { titulo } = props
    return (
        <Row>
            <Col span={24}>
                <Row justify='start'>
                    <Typography.Title
                        level={4}
                        style={{
                            margin: 0,
                        }}
                    >
                        {
                            titulo
                        }
                    </Typography.Title>
                </Row>
            </Col>
        </Row>
    )
}

Titulo.propTypes = {
    titulo: PropTypes.string.isRequired
}

export default Titulo