import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'

import { Typography, Input, Form, Button, Row, Col, message, Divider, Select } from 'antd'

import AlertWait from '../../pages/AlertWait'
import PersonaService from '../../service/PersonaService'
import CatalogoService from './../../service/CatalogoService'

const { Option } = Select

const personaService = new PersonaService()
const catalogoService = new CatalogoService()

const formItemLayout = { xs: 24, sm: 12, md: 12, lg: 12, xl: 12 }

const Persona = (props) => {

    const { navigate } = props
    const [load, setLoad] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [isSaving, setIsSaving] = useState(false)
    const [error, setError] = useState(null)
    const [persona, setPersona] = useState(null)
    const [tiposPersona, setTiposPersona] = useState([])
    const [tiposIdentificacion, setTiposIdentificacion] = useState([])
    const [actividades, setActividades] = useState([])

    const renderCatalogo = e => <Option key={e.catId} value={e.catId}>{e.catNombre}</Option>

    const get = async () => {
        setLoad(false)
        setIsLoading(true)
        try {
            const responses = await Promise
                .all([
                    catalogoService.getTiposPersona(navigate),
                    catalogoService.getTiposIdentificacion(navigate),
                    catalogoService.getActividadesEconomicas(navigate),
                    personaService.get(props)
                ])
            setTiposPersona(responses[0].data.listObject)
            setTiposIdentificacion(responses[1].data.listObject)
            setActividades(responses[2].data.listObject)
            setPersona(responses[3].data.listObject[0])
        } catch (errors) {
            console.log(errors)
            setError("No pudimos hacer la solicitud para obtener la persona")
        } finally {
            setIsLoading(false)
        }
    }

    const update = () => {
        setIsSaving(true)
        setTimeout(() => {
            setIsSaving(false)
            message.success('Datos actualizados correctamente')
        }, 3000)
    }

    useEffect(() => {
        if (load) get()
    })

    return (
        <>
            <Row>
                <Col span={24}>
                    <Row justify='start'>
                        <Typography.Title
                            level={4}
                            style={{
                                margin: 0,
                            }}
                        >PYME</Typography.Title>
                    </Row>
                </Col>
            </Row>
            <Divider></Divider>
            {
                isLoading && <AlertWait error={error} mensaje='Cargando informacin' isLoading={isLoading} />
            }
            {
                persona &&
                <Form
                    layout={'vertical'}
                    initialValues={persona}
                    onFinish={update}
                    disabled={isSaving}
                >
                    <Row gutter={[10, 10]}>
                        <Col span={24}>
                            <Form.Item label="Tipo de Persona"
                                name={['perTipoPersona', 'catId']}
                                rules={[
                                    {
                                        required: true,
                                        message: 'Seleccione el tipo de persona',
                                    },
                                ]}
                            >
                                <Select>
                                    {
                                        tiposPersona && tiposPersona.map(p => renderCatalogo(p))
                                    }
                                </Select>
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col {...formItemLayout}>
                            <Form.Item label="Tipo de Identificacin"
                                name='perIdTipoDoc'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Selecccione el tipo de identificacin',
                                    },
                                ]}
                            >
                                <Select>
                                    {
                                        tiposIdentificacion && tiposIdentificacion.map(p => renderCatalogo(p))
                                    }
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col {...formItemLayout}>
                            <Form.Item label="Identificacin"
                                name={['usuId', 'documento']}
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese su identificacin',
                                    },
                                ]}
                            >
                                <Input type='text' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col span={24}>
                            <Form.Item label="Razn Social"
                                name='perRazonSocial'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese la razn social',
                                    },
                                ]}
                            >
                                <Input type='text' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col {...formItemLayout}>
                            <Form.Item label="Tipo de Actividad"
                                name={['perActividad', 'catId']}
                                rules={[
                                    {
                                        required: true,
                                        message: 'Seleccione el tipo de actividad',
                                    },
                                ]}
                            >
                                <Select>
                                    {
                                        actividades && actividades.map(p => renderCatalogo(p))
                                    }
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col {...formItemLayout}>
                            <Form.Item label="N Clientes"
                                name='perClientes'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese el nmero de clientes',
                                    },
                                ]}
                            >
                                <Input type='number' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col span={24}>
                            <Form.Item label="Direccin"
                                name='perDireccion'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese la direccin',
                                    },
                                ]}
                            >
                                <Input type='text' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col {...formItemLayout}>
                            <Form.Item label="Telfono"
                                name='perTelefono'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese el telfono',
                                    },
                                ]}
                            >
                                <Input type='text' />
                            </Form.Item>
                        </Col>
                        <Col {...formItemLayout}>
                            <Form.Item label="Sitio WEB"
                                name='perSitioWeb'
                                rules={[
                                    {
                                        required: true,
                                        type: 'url',
                                        message: 'Ingrese el sitio web',
                                    },
                                ]}
                            >
                                <Input type='text' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col span={24}>
                            <Form.Item label="Descripcin"
                                name='perDescripcion'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese la descripcin',
                                    },
                                ]}
                            >
                                <Input type='text' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col span={24}>
                            <Form.Item label="Email"
                                name={['usuId', 'email']}
                                rules={[
                                    {
                                        required: true,
                                        type: 'email',
                                        message: 'Ingrese el email',
                                    },
                                ]}
                            >
                                <Input type='text' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col {...formItemLayout}>
                            <Form.Item label="Nombres"
                                name={['usuId', 'nombres']}
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese sus nombres',
                                    },
                                ]}
                            >
                                <Input type='text' />
                            </Form.Item>
                        </Col>
                        <Col {...formItemLayout}>
                            <Form.Item label="Apellidos"
                                name={['usuId', 'apellidos']}
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese sus apellidos',
                                    },
                                ]}
                            >
                                <Input type='text' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Form.Item>
                        <Button type='primary' htmlType='submit' loading={isSaving} disabled={isSaving} block>
                            {
                                isSaving ? 'Guardando...' : 'Actualizar'
                            }
                        </Button>
                    </Form.Item>
                </Form>
            }
        </>
    )
}

Persona.propTypes = {
    persona: PropTypes.object.isRequired,
    navigate: PropTypes.object.isRequired
}

export default Persona