import React, { useState, useEffect, useRef } from "react";
import { classNames } from "primereact/utils";
import { Route, useLocation } from "react-router-dom";

import AppTopbar from "./AppTopbar";
import AppFooter from "./AppFooter";
import AppRightPanel from "./AppRightPanel";

// import Dashboard from './components/Dashboard';
import FormLayoutDemo from "./components/FormLayoutDemo";
import InputDemo from "./components/InputDemo";
import FloatLabelDemo from "./components/FloatLabelDemo";
import InvalidStateDemo from "./components/InvalidStateDemo";
import ButtonDemo from "./components/ButtonDemo";
import TableDemo from "./components/TableDemo";
import ListDemo from "./components/ListDemo";
import TreeDemo from "./components/TreeDemo";
import PanelDemo from "./components/PanelDemo";
import OverlayDemo from "./components/OverlayDemo";
import MediaDemo from "./components/MediaDemo";
import MenuDemo from "./components/MenuDemo";
import MessagesDemo from "./components/MessagesDemo";
import FileDemo from "./components/FileDemo";
import ChartDemo from "./components/ChartDemo";
import MiscDemo from "./components/MiscDemo";
import Documentation from "./components/Documentation";
import IconsDemo from "./utilities/IconsDemo";
import CrudDemo from "./pages/CrudDemo";
import CalendarDemo from "./pages/CalendarDemo";
import TimelineDemo from "./pages/TimelineDemo";
import Invoice from "./pages/Invoice";
import Help from "./pages/Help";
import EmptyPage from "./pages/EmptyPage";
import BlocksDemo from "./components/BlocksDemo";
import { Tooltip } from "primereact/tooltip";

import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
import "primeflex/primeflex.css";
import "./App.scss";

import Inversiones from "./components/inversiones/Inversiones";
import MisInversiones from "./components/inversiones/MisInversiones";
import Inversion from "./components/inversiones/Inversion";
import Invertir from "./components/inversiones/Invertir";
import Reportes from "./components/Reportes";
import MiCuenta from "./components/cuenta/MiCuenta";
import Usuarios from "./components/usuario";
import Usuario from "./components/usuario/Usuario";
import Catalogos from "./components/catalogo";
import Catalogo from "./components/catalogo/Catalogo";
import Opciones from "./components/opcion";
import Perfiles from "./components/perfil";
import Perfil from "./components/perfil/Perfil";
import { getOpcionesMenu } from "./utils/Utils";
import ChangePassword from "./components/cuenta/ChangePassword";
import Solicitudes from "./components/solicitud/Solicitudes";

const App = (props) => {
  const [resetActiveIndex, setResetActiveIndex] = useState(null);
  const [staticMenuMobileActive, setStaticMenuMobileActive] = useState(false);
  const [sidebarStatic, setSidebarStatic] = useState(false);
  const [sidebarActive, setSidebarActive] = useState(false);
  const [menuActive, setMenuActive] = useState(false);
  const [configActive, setConfigActive] = useState(false);
  const [rightPanelActive, setRightPanelActive] = useState(false);
  const [searchActive, setSearchActive] = useState(false);
  const [topbarUserMenuActive, setTopbarUserMenuActive] = useState(false);
  const copyTooltipRef = useRef();
  const location = useLocation();

  const menu = getOpcionesMenu();

  let menuClick;
  let rightPanelClick;
  let configClick;
  let searchClick;
  let topbarUserMenuClick;

  useEffect(() => {
    copyTooltipRef &&
      copyTooltipRef.current &&
      copyTooltipRef.current.updateTargetEvents();
  }, [location]);

  useEffect(() => {
    if (staticMenuMobileActive) {
      blockBodyScroll();
    } else {
      unblockBodyScroll();
    }
  }, [staticMenuMobileActive]);

  useEffect(() => {
    setResetActiveIndex(true);
    setMenuActive(false);
  }, []);

  const onMenuItemClick = (event) => {
    if (!event.item.items) {
      setResetActiveIndex(true);
      hideOverlayMenu();
    }
    if (!event.item.items && (isSlim() || isHorizontal())) {
      setMenuActive(false);
    }
  };

  const onMenuClick = (event) => {
    if (menuActive && event.target.className === "layout-menu-container") {
      setResetActiveIndex(true);
      setMenuActive(false);
    }
    menuClick = true;
  };

  const blockBodyScroll = () => {
    if (document.body.classList) {
      document.body.classList.add("blocked-scroll");
    } else {
      document.body.className += " blocked-scroll";
    }
  };

  const unblockBodyScroll = () => {
    if (document.body.classList) {
      document.body.classList.remove("blocked-scroll");
    } else {
      document.body.className = document.body.className.replace(
        new RegExp(
          "(^|\\b)" + "blocked-scroll".split(" ").join("|") + "(\\b|$)",
          "gi"
        ),
        " "
      );
    }
  };

  const onMenuButtonClick = (event) => {
    menuClick = true;
    setTopbarUserMenuActive(false);
    setRightPanelActive(false);

    if (isMobile()) {
      setStaticMenuMobileActive(
        (prevStaticMenuMobileActive) => !prevStaticMenuMobileActive
      );
      if (staticMenuMobileActive) {
        blockBodyScroll();
      } else {
        unblockBodyScroll();
      }
    }
    event.preventDefault();
  };

  const isMobile = () => {
    return window.innerWidth <= 991;
  };

  const isHorizontal = () => {
    return "sideBar" === "horizontal";
  };

  const isSlim = () => {
    return "sideBar" === "slim";
  };

  const hideOverlayMenu = () => {
    setStaticMenuMobileActive(false);
    unblockBodyScroll();
  };

  const onRightPanelClick = () => {
    rightPanelClick = true;
  };

  const onRightPanelButtonClick = () => {
    setRightPanelActive((prevState) => !prevState);
    rightPanelClick = true;
  };

  const onTopbarSearchToggle = () => {
    setSearchActive((prevState) => !prevState);
    searchClick = true;
  };

  const onTopbarSearchClick = () => {
    searchClick = true;
  };

  const onTopbarUserMenuClick = () => {
    setTopbarUserMenuActive((prevState) => !prevState);
    topbarUserMenuClick = true;
  };

  const onDocumentClick = () => {
    if (!searchClick && searchActive) {
      setSearchActive(false);
      searchClick = false;
    }

    if (!topbarUserMenuClick && topbarUserMenuActive) {
      setTopbarUserMenuActive(false);
      topbarUserMenuClick = false;
    }

    if (!rightPanelClick && rightPanelActive) {
      setRightPanelActive(false);
    }

    if (!configClick && configActive) {
      setConfigActive(false);
    }

    if (!menuClick) {
      if (isSlim() || isHorizontal()) {
        setResetActiveIndex(true);
        setMenuActive(false);
      }

      if (staticMenuMobileActive) {
        hideOverlayMenu();
      }

      unblockBodyScroll();
    }

    searchClick = false;
    topbarUserMenuClick = false;
    rightPanelClick = false;
    configClick = false;
    menuClick = false;
  };

  const onSidebarMouseOver = () => {
    setSidebarActive(!isMobile());
  };

  const onSidebarMouseLeave = () => {
    setSidebarActive(false);
  };

  const onToggleMenu = (event) => {
    menuClick = true;
    setSidebarStatic((prevState) => !prevState);

    event.preventDefault();
  };

  const onRootMenuItemClick = () => {
    setMenuActive((prevMenuActive) => !prevMenuActive);
  };

  const layoutClassName = classNames(
    "layout-wrapper",
    {
      "layout-sidebar": "sidebar",
      "layout-static": "sidebar" && sidebarStatic,
      "layout-horizontal": "sidebar" === "horizontal",
      "layout-rightpanel-active": rightPanelActive,
      "layout-slim": "sidebar" === "slim",
      "layout-mobile-active": staticMenuMobileActive,
      "p-input-filled": "filled",
      "p-ripple-disabled": false,
    },
    "layout-menu-light layout-topbar-light"
  );

  return (
    <div className={layoutClassName} onClick={onDocumentClick} style={{ backgroundColor: '#F0F0F0' }}>
      <Tooltip
        ref={copyTooltipRef}
        target=".block-action-copy"
        position="bottom"
        content="Copied to clipboard"
        event="focus"
      />

      <AppTopbar
        topbarScheme={"light"}
        onRightPanelButtonClick={onRightPanelButtonClick}
        searchActive={searchActive}
        onTopbarSearchToggle={onTopbarSearchToggle}
        onTopbarSearchClick={onTopbarSearchClick}
        topbarUserMenuActive={topbarUserMenuActive}
        onTopbarUserMenuClick={onTopbarUserMenuClick}
        menu={menu}
        menuActive={menuActive}
        onRootMenuItemClick={onRootMenuItemClick}
        mobileMenuActive={staticMenuMobileActive}
        onMenuItemClick={onMenuItemClick}
        menuMode={"sidebar"}
        sidebarStatic={sidebarStatic}
        sidebarActive={sidebarActive}
        onSidebarMouseOver={onSidebarMouseOver}
        onSidebarMouseLeave={onSidebarMouseLeave}
        onToggleMenu={onToggleMenu}
        onMenuButtonClick={onMenuButtonClick}
        resetActiveIndex={resetActiveIndex}
        onMenuClick={onMenuClick}
      />

      <AppRightPanel onRightPanelClick={onRightPanelClick} />

      <div className="layout-main">
        <div className="layout-content">
          <Route path="/" exact component={Inversiones} />
          <Route path="/inversiones/:filtro" exact component={Solicitudes} />
          <Route path="/misinversiones/:filtro" exact component={MisInversiones} />
          <Route path="/inversion/:id" exact component={Inversion} />
          <Route path="/invertir/:id" exact component={Invertir} />
          <Route path="/reportes" exact component={Reportes} />
          <Route path="/cuenta" exact component={MiCuenta} />
          <Route path="/change" exact component={ChangePassword} />
          <Route path="/usuarios" exact component={Usuarios} />
          <Route path="/usuario/:id" exact component={Usuario} />
          <Route path="/catalogos" exact component={Catalogos} />
          <Route path="/catalogo/:id/:nombre" exact component={Catalogo} />
          <Route path="/opciones" exact component={Opciones} />
          <Route path="/perfiles" exact component={Perfiles} />
          <Route path="/perfil/:id/:nombre" exact component={Perfil} />

          <Route path="/start/documentation" component={Documentation} />
          <Route path="/uikit/formlayout" component={FormLayoutDemo} />
          <Route path="/uikit/floatlabel" component={FloatLabelDemo} />
          <Route path="/uikit/input" component={InputDemo} />
          <Route path="/uikit/invalidstate" component={InvalidStateDemo} />
          <Route path="/uikit/button" component={ButtonDemo} />
          <Route path="/uikit/table" component={TableDemo} />
          <Route path="/uikit/list" component={ListDemo} />
          <Route path="/uikit/tree" component={TreeDemo} />
          <Route path="/uikit/panel" component={PanelDemo} />
          <Route path="/uikit/overlay" component={OverlayDemo} />
          <Route path="/uikit/menu" component={MenuDemo} />
          <Route path="/uikit/message" component={MessagesDemo} />
          <Route path="/uikit/media" component={MediaDemo} />
          <Route path="/uikit/file" component={FileDemo} />
          <Route
            path="/uikit/chart"
            render={() => <ChartDemo colorMode={"light"} location={location} />}
          />
          <Route path="/uikit/misc" component={MiscDemo} />
          <Route path="/utilities/icons" component={IconsDemo} />
          <Route path="/pages/crud" component={CrudDemo} />
          <Route path="/pages/calendar" component={CalendarDemo} />
          <Route
            path="/pages/help"
            render={() => <Help colorScheme={"light"} />}
            location={location}
          />
          <Route path="/pages/invoice" component={Invoice} />
          <Route path="/pages/empty" component={EmptyPage} />
          <Route path="/pages/timeline" component={TimelineDemo} />
          <Route path="/uiblocks/blocks" component={BlocksDemo} />
        </div>

        <AppFooter />
      </div>
      <div className="layout-mask modal-in"></div>
    </div>
  );
};

export default App;
