
import React, { useState, useEffect } from 'react'
import { useHistory, useParams } from 'react-router-dom'
import './inversion.css'
import { Typography, Badge, Divider, Button, Spin, Progress, message, Row, Col, Alert, Card, Avatar, Form, Image, Statistic, InputNumber, Upload, Result, Drawer, Rate, Popconfirm } from 'antd'
import * as anticons from '@ant-design/icons'
import { formatoMoneda, getCalificaciones, getURLImagenRandom, getURLImagenPYMERandom, getEstiloFerificada, getResponsable, getDatosUsuario, renderError } from '../../utils/Utils'
import { getOrigenes } from '../../service/OrigenServices'

import OfertaInversionService from '../../service/OfertaInversionService'
import SolicitudInversionService from '../../service/SolicitudInversionService'
import TablaAmortizacionService from '../../service/TablaAmortizacionService'

import TablaAmortizacion from '../tablaAmortizacion/TablaAmortizacion'
import ResumenInversion from './ResumenInversion'
import Inversionista from '../inversionista/Inversionista'
import AlertWait from '../../pages/AlertWait'

const { Meta } = Card
const { Dragger } = Upload
const { Title, Text } = Typography

const calificacion = {
  value: 3
}
const pasos = [
  {
    id: 1,
    titulo: 'CALCULA TU INVERSIN'
  },
  {
    id: 2,
    titulo: 'DATOS DEL INVERSIONISTA'
  },
  {
    id: 3,
    titulo: 'DECLARACIN DE ORIGEN DE FONDOS'
  },
  {
    id: 4,
    titulo: 'CONFIRMA TU INVERSIN'
  },
  {
    id: 5,
    titulo: 'Inversin Confirmada'
  }
]

const ofertaInversionService = new OfertaInversionService()
const solicitudInversionService = new SolicitudInversionService()
const tablaAmortizacionService = new TablaAmortizacionService()

const Inversion = () => {

  const navigate = useHistory()
  const { id } = useParams()
  const [inversion, setInversion] = useState(null)
  const [solicitudInversion, setSolicitudInversion] = useState({ slcMonto: 200 })
  const [tablaAmortizacion, setTablaAmortizacion] = useState([])
  const [load, setLoad] = useState(true)
  const [isLoading, setIsLoading] = useState(false)
  const [isCalculando, setIsCalculando] = useState(false)
  const [isSending, setIsSending] = useState(false)
  const [inversionEnviada, setInversionEnviada] = useState(false)
  const [error, setError] = useState(null)
  const [formSimular] = Form.useForm();

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [paso, setPaso] = useState(0);

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  const siguientePaso = () => {
    setPaso(paso + 1);
  }

  const anteriorPaso = () => {
    setPaso(paso - 1);
  }

  const Invertir = async (c) => {
    setIsSending(true)
    setPaso(pasos.length - 1)
    try {
      const { data } = await solicitudInversionService.put({ navigate, solicitudInversion })
      console.log(data)
      setSolicitudInversion(true)
      message.success('Solicitud registrada exitosamente')
    }
    catch (error) {
      const { status, data } = error.response
      switch (status) {
        case 409:
          setError(data.map(e => renderError(e)))
          break
        case 500:
          setError(data)
          break
        default:
          break
      }
    } finally {
      setIsSending(false)
    }
  }

  const CalcularInversion = async (solicitudInversion) => {
    const datosCalcular = {
      slcMonto: solicitudInversion.slcMonto,
      slcNumCuotas: inversion.oinPlazo,
      slcTazaInt: inversion.oinTir
    }
    setIsCalculando(true)
    try {
      const { data } = await tablaAmortizacionService.get({ navigate, solicitudInversion: { ...datosCalcular } })
      const tabla = data.listObject[0]
      setTablaAmortizacion(tabla)
      const _solicitudInversion = {
        slcMonto: solicitudInversion.slcMonto,
        slcCuotaMensual: tabla.length > 0 ? tabla[0].cuotaMensual : 0,
        slcNumCuotas: inversion.oinPlazo,
        slcCapMasInt: tabla.length > 0 ? tabla.reduce((p, c) => p + c.cuotaMensual, 0) : 0,
        slcIdInversionista: getResponsable(),
        slcIdOfertaInv: id,
        slcIdEstado: 93
      }
      setSolicitudInversion(_solicitudInversion)
      setIsCalculando(false)
    }
    catch (error) {
      setIsCalculando(false)
      setError('No fue posible realizar el clculo')
    }
  }

  const Calcular = (values) => {
    if (!inversionEnviada) {
      setPaso(0)
    }
    showModal(true)
    CalcularInversion(values)
  }

  const renderTablaAmortizacion = () => {
    const props = {
      tabla: tablaAmortizacion,
      cuota: solicitudInversion.slcMonto ? parseFloat(solicitudInversion.slcMonto) : 0,
      plazo: inversion.oinPlazo || 0,
      isLoading: isCalculando
    }
    return <TablaAmortizacion {...props}></TablaAmortizacion>
  }

  const renderResumenInversion = () => {
    const props = {
      cuota: solicitudInversion.slcMonto ? parseFloat(solicitudInversion.slcMonto) : 0
    }
    return <ResumenInversion {...props}></ResumenInversion>
  }

  const renderSimulador = (props) => {
    const { etiqueta } = props
    return (
      <Row>
        <Col span={24}>
          <Form
            layout={'vertical'}
            form={formSimular}
            initialValues={
              {
                slcMonto: 200
              }
            }
            onFinish={Calcular}
          >
            <Row gutter={[10, 10]} align='bottom' justify='center'>
              <Col>
                <Form.Item label="Inversin"
                  name="slcMonto"
                  rules={[
                    {
                      required: true,
                      message: 'Ingrese el monto de su inversin',
                    },
                  ]}>
                  <InputNumber
                    label='Inversin'
                    formatter={(value) => `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                    parser={(value) => value.replace(/\$\s?|(,*)/g, '')}
                  />
                </Form.Item>
              </Col>
              <Col>
                <Form.Item>
                  <Button type='primary' htmlType='submit' loading={isCalculando}>{etiqueta}</Button>
                </Form.Item>
              </Col>
            </Row>
            <Row justify='center'>
              <Col>
                <Typography.Text type='secondary'>
                  Inversin mnima $200.00
                </Typography.Text>
              </Col>
            </Row>
          </Form>
        </Col>
      </Row >
    )
  }

  const renderInversion = (i) => {
    const { oinId, oinCapital, oinDetalle, oinInversionistas, oinPlazo, oinRecaudado, oinTir, oinPersona } = i
    return (
      <Row gutter={20}>
        <Col xs={24} sm={14} md={14} lg={14} xl={14}>
          <Badge.Ribbon text={
            <span {...getEstiloFerificada()}>Solicitud Verificada <anticons.CheckCircleOutlined></anticons.CheckCircleOutlined></span>
          }>
            <Card
              bordered={false}
              description={{ oinDetalle }}
              cover={
                <Image src={getURLImagenPYMERandom(oinId)} alt="" width={'100%'} heigh={'100%'} />
              }
            >
              <Meta
                avatar={
                  <>
                    <Avatar src={getURLImagenRandom(oinId)} /><br />
                    <Rate tooltips={getCalificaciones()} value={3} /><br />
                    {calificacion.value ? <span className="ant-rate-text">{getCalificaciones()[calificacion.value - 1]}</span> : ''}
                  </>
                }
                title={`${oinPersona.usuId.nombres !== undefined ? oinPersona.usuId.nombres : ''} ${oinPersona.usuId.apellidos !== undefined ? oinPersona.usuId.apellidos : ''}`}
                description={oinDetalle}
              />
            </Card>
          </Badge.Ribbon>
        </Col>
        <Col xs={24} sm={10} md={10} lg={10} xl={10}>
          <Row>
            <Col span={12}>
              <Statistic
                value={
                  oinRecaudado && oinCapital ?
                    (oinRecaudado || 0) * 100 / (oinCapital || 0) : 0
                }
                precision={2}
                suffix="%"
              />
            </Col>
            <Col span={12}>
              <Row justify='end'>
                <Statistic
                  value={formatoMoneda(oinCapital || 0)}
                />
              </Row>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Progress
                strokeLinecap='square'
                strokeColor={{
                  '0%': '#8A05BE',
                  '100%': '#006e2c',
                }}
                percent={((oinRecaudado || 0) * 100 / (oinCapital || 0)).toFixed(2)} showInfo={false} trailColor={'#ccc'} />
            </Col>
          </Row>
          <Row>
            <Col span={12}>
              <Statistic
                title={formatoMoneda(oinRecaudado || 0)}
                value={'Recaudado'}
                valueStyle={{
                  color: '#000',
                  fontSize: '1em'
                }}
              />
            </Col>
            <Col span={12}>
              <Row justify='end'>
                <Statistic
                  title={
                    <div style={{
                      textAlign: 'right'
                    }}>
                      {formatoMoneda(oinCapital || 0 - oinRecaudado || 0)}
                    </div>
                  }
                  value={'Por Recaudar'}
                  valueStyle={{
                    color: '#000',
                    fontSize: '1em'
                  }}
                />
              </Row>
            </Col>
          </Row>
          <Divider><span style={{ textTransform: 'uppercase', letterSpacing: '1px' }}>Datos para la inversin</span></Divider>
          <Row style={{ textAlign: 'center', background: 'rgba(41, 127, 255, 0.04)', paddingTop: '15px', borderRadius: '12px' }}>
            <Col span={8} style={{ borderRight: 'solid 1px #f0f0f0' }}>
              <Statistic
                title={
                  <div>Plazo</div>
                }
                value={oinPlazo}
                precision={0}
                suffix={
                  <Typography.Paragraph>
                    <Typography.Text strong>
                      Meses
                    </Typography.Text>
                  </Typography.Paragraph>
                }
                valueStyle={
                  {
                    fontSize: '1.1em',
                    fontWeight: 600,
                    color: 'rgba(0, 0, 0, 0.85)'
                  }
                }
              />
            </Col>
            <Col span={8} style={{ borderRight: 'solid 1px #f0f0f0' }}>
              <Statistic
                title={
                  <div>TIR %</div>
                }
                value={oinTir}
                precision={2}
                valueStyle={
                  {
                    fontSize: '1.1em',
                    fontWeight: 600,
                    color: 'rgba(0, 0, 0, 0.85)'
                  }
                }
              />
            </Col>
            <Col span={8}>
              <Statistic
                title={
                  <div>Inversionistas</div>
                }
                value={oinInversionistas}
                precision={0}
                valueStyle={
                  {
                    fontSize: '1.1em',
                    fontWeight: 600,
                    color: 'rgba(0, 0, 0, 0.85)'
                  }
                }
              />
            </Col>
          </Row>
          <Divider><span style={{ textTransform: 'uppercase', letterSpacing: '1px' }}>Calcula tu Ganancia</span></Divider>
          {
            renderSimulador({ etiqueta: 'Simular' })
          }
        </Col>
      </Row>
    )
  }

  const fetchInversion = async () => {
    setError(null)
    setLoad(false)
    setIsLoading(true)
    try {
      const { data } = await ofertaInversionService.get({ navigate, ofertaInversion: { oinId: id } })
      const ofertaInversion = data.listObject[0]
      setInversion(ofertaInversion)
    } catch (e) {
      console.log(e)
      setError("No pudimos hacer la solicitud para obtener la oferta de invesin")
    } finally {
      setIsLoading(false)
    }
  }


  useEffect(() => {
    if (load) fetchInversion()
  })

  return (
    <>
      {
        isLoading &&
        <Row>
          <Col span={24}>
            <Row justify='center'>
              <Spin size="large" />
            </Row>
          </Col>
        </Row>
      }
      {
        error &&
        <Row>
          <Col span={24} style={{ padding: '0 0 15px 0' }}>
            <Alert description={error} type='warning' showIcon banner></Alert>
          </Col>
        </Row>
      }
      <Row>
        <Col span={24}>
          <Typography>
            <Title level={4}>
              Inversin
            </Title>
            {
              inversion &&
              <Text type='secondary'>
                {
                  inversion.oinDetalle
                }
              </Text>
            }
          </Typography>
        </Col>
      </Row>
      <Divider></Divider>
      <Row justify='center'>
        <Col xs={24} sm={24} md={24} lg={24} xl={16}>
          {
            inversion && renderInversion(inversion)
          }
        </Col>
      </Row>

      <Drawer
        title={
          <Row justify='center'>
            <Col xs={24} sm={24} md={24} lg={24} xl={16}>
              <Typography.Paragraph>
                <Typography.Title level={4}>
                  Inversin
                </Typography.Title>
              </Typography.Paragraph>
              <Typography.Paragraph>
                <Typography.Text type='secondary'>
                  Tu Progreso {(paso) * (100 / (pasos.length - 1))} %
                </Typography.Text>
              </Typography.Paragraph>
              <Typography.Paragraph>

                <Progress
                  strokeLinecap='square'
                  strokeColor={{
                    '0%': '#0E61D1',
                    '100%': '#006e2c',
                  }}
                  percent={(paso) * 25}
                  showInfo={false}
                  trailColor={'#ccc'} size='default' />
              </Typography.Paragraph>
              <Typography.Paragraph style={{ textAlign: 'center', letterSpacing: '1px' }}>
                <Typography.Title level={4} style={{ color: '#0E61D1' }}>
                  {pasos[paso].titulo}
                </Typography.Title>
              </Typography.Paragraph>
            </Col>
          </Row >
        }
        headerStyle={{
          padding: '10px'
        }}
        placement="bottom"
        closable={true}
        onClose={handleCancel}
        open={isModalOpen}
        height={"94%"}
      >
        <Row justify='center'>
          <Col xs={24} sm={24} md={24} lg={24} xl={16}>
            {
              paso === 0 ?
                <>
                  {
                    renderSimulador({ etiqueta: 'Calcular' })
                  }
                  <div className='steps-content'>
                    {tablaAmortizacion && tablaAmortizacion.length > 0 ? renderTablaAmortizacion() : <></>}
                    <Typography.Paragraph>
                      * Al dar clic en Siguiente confirmo haber aceptado previamente los <a href='/files/politicasuso.pdf' target={'_blank'}><Typography.Text strong>Acuerdos especficos de uso del sitio web</Typography.Text></a> como usuario inversionista y entiendo los riesgos que se desprenden de este tipo de inversiones.
                      <br />
                      ** KOYN se encarga de conectar a emprendedores con inversionistas. KOYN no se responsabiliza por el pago de los crditos concedidos a los solicitantes.
                    </Typography.Paragraph>
                  </div>
                </>
                : paso === 1 ?
                  <div className='steps-content'>
                    <Inversionista inversionista={{ invId: getResponsable() }} navigate={navigate} />
                  </div>
                  : paso === 2 ?
                    <div className='steps-content'>
                      <Typography.Paragraph>
                        Yo, <Typography.Text strong>"NOMBRE INVERSIONISTA"</Typography.Text> portador de la cdula de ciudadana No. <Typography.Text strong>"IDENTIFICACION INVERSIONISTA"</Typography.Text>, por sus propios y personales derechos, manifiesto que la informacin descrita en el formulario de usuario en calidad de inversionista para la operacin de financiamiento colaborativo con inters a plazo fijo, utilizando la plataforma virtual de KOYN es cierta, y realizo las siguientes declaraciones de fuentes u origen de fondos y actividades lcitas:
                      </Typography.Paragraph>
                      <ol>
                        {
                          getOrigenes().map(o => <li key={o.id}>{o.value}</li>)
                        }
                      </ol>
                      <Typography.Paragraph>
                        <Typography.Text strong italic>
                          Los datos aqu consignados obedecen a la realidad. Al dar clic en Siguiente declaro (amos) haber ledo, entendido y aceptado libremente el presente documento.
                        </Typography.Text>
                      </Typography.Paragraph>
                    </div>
                    : paso === 3 ?
                      <>
                        <Typography.Paragraph>
                          <Typography.Title level={5}>
                            Transfiere los fondos a la siguiente cuenta y asegura tu inversin:
                          </Typography.Title>
                        </Typography.Paragraph>
                        <div className='steps-content' style={{ textAlign: 'center' }}>
                          <Typography.Paragraph>
                            {
                              tablaAmortizacion && tablaAmortizacion.length > 0 ? renderResumenInversion() : <></>
                            }
                            <Typography.Text strong>Cuenta Corriente: </Typography.Text>123456789
                            <br /><Typography.Text strong>BANCO DE GUAYAQUIL</Typography.Text>
                            <br /><Typography.Text strong>A nombre de: </Typography.Text>KOYN
                            <br /><Typography.Text strong>RUC: </Typography.Text>099999999001
                            <br /><Typography.Text strong>Correo: </Typography.Text>info@koyn.com
                            <br /><Typography.Text strong>Telfono: </Typography.Text>0999999999
                          </Typography.Paragraph>
                          <Dragger>
                            <p className="ant-upload-drag-icon">
                              <anticons.InboxOutlined />
                            </p>
                            <p className="ant-upload-text">Cargar comprobante</p>
                            <p className="ant-upload-hint">
                              Adjunta tu comprobante de transferencia
                            </p>
                          </Dragger><br />
                          <Popconfirm
                            title="Confirmar Inversin"
                            description="Seguro(a) que desea enviar tu solicitud de inversin?"
                            onConfirm={() => Invertir(inversion.oinId)}
                            okText="Si"
                            cancelText="No"
                            disabled={isSending || inversionEnviada}
                            loading={isSending}
                          >
                            <Button type='primary' disabled={isSending || inversionEnviada} loading={isSending}>Confirmar Inversin</Button>
                          </Popconfirm>
                        </div>
                        <Alert
                          description={
                            <div style={{ textAlign: 'justify' }}>
                              Esta cuenta est a nombre del emprendedor, sin embargo, es administrada por KOYN quien impide el acceso a los fondos hasta que se complete el total solicitado y se firmen los documentos de respaldo.
                            </div>
                          }
                          type="info"
                          showIcon
                        /><br />
                        <Typography.Paragraph>
                          <Typography.Text type='secondary' italic>
                            KOYN se encarga de conectar a emprendedores con inversionistas.
                            <br />KOYN no se responsabiliza por el pago de los crditos concedidos a los solicitantes.
                          </Typography.Text>
                        </Typography.Paragraph>
                      </>
                      : paso === 4 ?
                        <div className='steps-content' style={{ textAlign: 'center' }}>
                          {inversionEnviada === true && <Result
                            status="success"
                            title="Inversin enviada existosamente"
                            subTitle="Inversin enviada existosamente"
                            extra={[]}
                          />
                          }
                        </div>
                        : <></>
            }
            <Row gutter={[10, 10]} justify='center'>
              <Col>
                <Button disabled={!(paso > 0) || inversionEnviada} onClick={() => anteriorPaso()}>Anterior</Button>
              </Col>
              <Col>
                <Button disabled={!(paso < (pasos.length - 2)) || inversionEnviada} onClick={() => siguientePaso()}>Siguiente</Button>
              </Col>
            </Row>
            <Row>
              <Col span={24} style={{ padding: '15px 0' }}>
                {
                  error && <AlertWait error={error} isLoading={isLoading} />
                }
              </Col>
            </Row>
          </Col>
        </Row>
      </Drawer >

    </>
  )
}

const comparisonFn = function (prevProps, nextProps) {
  return (prevProps.location.pathname === nextProps.location.pathname) && (prevProps.colorMode === nextProps.colorMode);
};

export default React.memo(Inversion, comparisonFn);