import React from 'react'
import { useHistory } from 'react-router-dom'
import { Row, Col } from 'antd'

const logo = {
    fontSize: "4.5em",
    padding: 0,
    margin: 0,
}

const Logo = (props) => {
    const history = useHistory()
    return (
        <Row justify='center' {...props}>
            <Col>
                <button onClick={() => history.push("/")} className="logo p-link">
                    <div className="ck ck-bold" style={logo}>
                        <span className="ck-purple">ko</span>
                        <span className="ck-black">yn</span>
                    </div>
                </button>
            </Col>
        </Row>
    )
}

export default Logo