import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { Input, Form, Button, Row, Col, DatePicker, message, Divider, Checkbox, Select, InputNumber } from 'antd'
import { formatoFechaDate, getLocale } from './../utils/Utils'
import PasswordChecklist from 'react-password-checklist'
import AlertWait from './AlertWait'
import Logo from '../components/Logo'
import md5 from 'md5'
import CuentaService from './../service/CuentaService'
import CatalogoService from './../service/CatalogoService'

const cuentaService = new CuentaService()
const catalogoService = new CatalogoService()
const formItemLayout = { xs: 24, sm: 12, md: 12, lg: 12, xl: 12 }
const { Option } = Select

const renderCatalogo = e => <Option key={e.catId} value={e.catId}>{e.catNombre}</Option>

const Register = () => {

    const navigate = useHistory()
    const [load, setLoad] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [error, setError] = useState(null)
    const [form] = Form.useForm()

    const [password, setPassword] = useState('')
    const [passwordConfirm, setPasswordConfirm] = useState('')
    const [fuentesIngreso, setFuentesIngreso] = useState([])
    const [tiposIdentificacion, setTiposIdentificacion] = useState([])

    const fetchData = async () => {
        setLoad(false)
        setIsLoading(true)
        try {
            const responses = await Promise
                .all([
                    catalogoService.getTiposIdentificacion(navigate),
                    catalogoService.getFuentesIngreso(navigate)
                ])
            if (responses) {
                setTiposIdentificacion(responses[0].data.listObject)
                setFuentesIngreso(responses[1].data.listObject)
            }
        }
        catch (e) {
            console.log(e)
            setError("Ocurrio en error al consultar los catlogos")
            // const { response } = error
            // setError(response.status === 409 ? response.data[0].messageText : "No pudimos hacer la solicitud para obtener el catlogo")
        }
        finally {
            setIsLoading(false)
        }
    }


    useEffect(() => {
        if (load) fetchData()
    })

    const renderError = (e) => <div key={e.messageCode}>{e.messageText}</div>
    const renderCatalogoValor = e => <Option key={e.catValor} value={e.catId}>{e.catNombre}</Option>

    const login = () => {
        navigate.push('/login')
    }
    const registrarPersona = () => {
        navigate.push('/registerpyme')
    }

    const registrar = (values) => {
        setError('')
        const fechaNacimiento = values.invFecNac && formatoFechaDate(values.invFecNac._d)
        const nuevoInversionista = { ...values, invPassword: md5(values.invPassword), invFecNac: fechaNacimiento }
        setIsLoading(true)
        cuentaService
            .register({ datos: { ...nuevoInversionista } })
            .then(response => {
                const { data } = response
                if (data && data.listObject.length > 0 && data.listObject[0].invId) {
                    message.success('Informacin registrada correctamente')
                }
                setIsLoading(false)
                navigate.push('/login')
            })
            .catch(error => {
                setIsLoading(false)
                const { status, data } = error.response
                switch (status) {
                    case 409:
                        setError(data.map(e => renderError(e)))
                        break
                    case 500:
                        setError(data)
                        break
                    default:
                        break
                }
            })
    }

    return (
        <Row justify='center' style={{ backgroundColor: '#F0F0F0' }}>
            <Col xs={22} sm={22} md={18} lg={12} xl={12}>
                <Logo />
                <Divider><span style={{ textTransform: 'uppercase', letterSpacing: '1px' }}>Registrarme como</span></Divider>
                <Row gutter={[10, 10]} style={{ paddingBottom: '20px' }}>
                    <Col span={12}>
                        <Button type='primary' block>INVERSIONISTA</Button>
                    </Col>
                    <Col span={12}>
                        <Button type='default' onClick={(e) => registrarPersona()} block>PERSONA</Button>
                    </Col>
                </Row>
                {
                    isLoading ? <AlertWait tipo='warning' error={error} mensaje={<>Espere un momento...</>} isLoading={isLoading} />
                        :
                        <Form
                            layout={'vertical'}
                            form={form}
                            initialValues={
                                {
                                    invNombres: "",
                                    invApellidos: "",
                                    invIdTipoDoc: "",
                                    invDocumento: "",
                                    invDireccion: "",
                                    invCiudad: "",
                                    invPais: "",
                                    invFecNac: "",
                                    invTelefono: "",
                                    invEmail: "",
                                    invPassword: "",
                                    invPasswordConfirm: "",
                                    invIdFuenteIngresos: "",
                                    invCargo: "",
                                    invIngresos: "",
                                    invTerCon: false
                                }
                            }
                            onFinish={registrar}
                        >
                            <Row gutter={[10, 10]}>
                                <Col {...formItemLayout}>
                                    <Form.Item label="Nombres"
                                        name='invNombres'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Ingrese sus nombres',
                                            },
                                        ]}
                                    >
                                        <Input id="invNombres" name="invNombres" label='Nombres' type='text' />
                                    </Form.Item>
                                </Col>
                                <Col {...formItemLayout}>
                                    <Form.Item label="Apellidos"
                                        name='invApellidos'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Ingrese sus apellidos',
                                            },
                                        ]}
                                    >
                                        <Input id="invApellidos" name="invApellidos" label='Apellidos' type='text' />
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row gutter={[10, 10]}>
                                <Col {...formItemLayout}>
                                    <Form.Item label="Tipo de Identificacin"
                                        name='invIdTipoDoc'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Ingrese el tipo de identificacin',
                                            },
                                        ]}
                                    >
                                        <Select>
                                            {
                                                tiposIdentificacion && tiposIdentificacion.map(p => renderCatalogoValor(p))
                                            }
                                        </Select>
                                    </Form.Item>
                                </Col>
                                <Col {...formItemLayout}>
                                    <Form.Item label="Identificacin"
                                        name='invDocumento'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Ingrese su identificacin',
                                            },
                                        ]}
                                    >
                                        <Input id="invDocumento" name="invDocumento" label='Identificacin' type='text' />
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row gutter={[10, 10]}>
                                <Col {...formItemLayout}>
                                    <Form.Item label="Pas"
                                        name='invPais'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Ingrese el pas',
                                            },
                                        ]}
                                    >
                                        <Input id="invPais" name="invPais" label='Pas' type='text' />
                                    </Form.Item>
                                </Col>
                                <Col {...formItemLayout}>
                                    <Form.Item label="Ciudad"
                                        name='invCiudad'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Ingrese la ciudad',
                                            },
                                        ]}
                                    >
                                        <Input id="invCiudad" name="invCiudad" label='Ciudad' type='text' />
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row gutter={[10, 10]}>
                                <Col span={24}>
                                    <Form.Item label="Direccin"
                                        name='invDireccion'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Ingrese su direccin',
                                            },
                                        ]}
                                    >
                                        <Input id="invDireccion" name="invDireccion" label='Direccin' type='text' />
                                    </Form.Item>
                                </Col>
                            </Row>

                            <Row gutter={[10, 10]}>
                                <Col {...formItemLayout}>
                                    <Form.Item label="Fecha de Nacimiento"
                                        name='invFecNac'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Ingrese la fecha de nacimiento',
                                            },
                                        ]}
                                    >
                                        <DatePicker id="invFecNac" name='invFecNac' format={['DD/MM/YYYY']} locale={getLocale()} style={{ width: '100%' }}></DatePicker>
                                    </Form.Item>
                                </Col>
                                <Col {...formItemLayout}>
                                    <Form.Item label="Telfono"
                                        name='invTelefono'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Ingrese el telfono',
                                            },
                                        ]}
                                    >
                                        <Input id="invTelefono" name="invTelefono" label='Telfono' type='text' />
                                    </Form.Item>
                                </Col>
                            </Row>

                            <Row gutter={[10, 10]}>
                                <Col span={8}>
                                    <Form.Item label="Principal Fuente de ingresos"
                                        name='invIdFuenteIngresos'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Seleccione la fuente de ingresos',
                                            },
                                        ]}
                                    >
                                        <Select name='invIdFuenteIngresos'>
                                            {
                                                fuentesIngreso && fuentesIngreso.map(p => renderCatalogo(p))
                                            }
                                        </Select>
                                    </Form.Item>
                                </Col>
                                <Col span={8}>
                                    <Form.Item label="Cargo"
                                        name='invCargo'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Ingrese el cargo',
                                            },
                                        ]}
                                    >
                                        <Input />
                                    </Form.Item>
                                </Col>
                                <Col span={8}>
                                    <Form.Item label="Ingresos mensuales aproximados"
                                        name='invIngresos'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Complete los ingresos',
                                            },
                                        ]}
                                    >
                                        <InputNumber
                                            formatter={(value) => `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                                            parser={(value) => value.replace(/\$\s?|(,*)/g, '')}
                                            style={{ width: '100%' }}
                                        />
                                    </Form.Item>
                                </Col>
                            </Row>

                            <Row gutter={[10, 10]}>
                                <Col span={24}>
                                    <Form.Item label="Email"
                                        name='invEmail'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Ingrese el email',
                                            },
                                        ]}
                                    >
                                        <Input id="invEmail" name="invEmail" label='Email' type='text' />
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row gutter={[10, 10]}>
                                <Col {...formItemLayout}>
                                    <Form.Item label="Contrasea"
                                        name='invPassword'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Ingrese la contrasea',
                                            },
                                        ]}
                                    >
                                        <Input.Password id="invPassword" name="invPassword" label='Contrasea' onChange={e => setPassword(e.target.value)} />
                                    </Form.Item>
                                </Col>
                                <Col {...formItemLayout}>
                                    <Form.Item label="Confirme la contrasea"
                                        name='invPasswordConfirm'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Confirme la contrasea',
                                            },
                                        ]}
                                    >
                                        <Input.Password id="invPasswordConfirm" name="invPasswordConfirm" label='Confirme la contrasea' onChange={e => setPasswordConfirm(e.target.value)} />
                                    </Form.Item>
                                </Col>
                                <Col span={24}>
                                    <PasswordChecklist
                                        rules={["minLength", "specialChar", "number", "capital", "match"]}
                                        minLength={8}
                                        value={password}
                                        valueAgain={passwordConfirm}
                                        messages={{
                                            minLength: "La contrasea tiene ms de 8 caracteres.",
                                            specialChar: "La contrasea tiene caracteres especiales.",
                                            number: "La contrasea tiene un nmero.",
                                            capital: "La contrasea tiene una letra mayscula.",
                                            match: "Las contraseas coinciden.",
                                        }}
                                        onChange={(isValid) => { }}
                                    />
                                </Col>
                                <Col span={24}>
                                    <Form.Item
                                        name='invTerCon'
                                        valuePropName="checked"
                                        rules={[
                                            {
                                                validator: (_, value) =>
                                                    value ? Promise.resolve() : Promise.reject(new Error('Acepte los trminos y condiciones y el acuerdo de uso de la plataforma')),
                                            },
                                        ]}
                                    >
                                        <Checkbox id='invTerCon' name='invTerCon'>
                                            Estoy de acuerdo y acepto los <a href='/files/terminoscondiciones.pdf' target={'_blank'}>Trminos y Condiciones</a> y el <a href='/files/politicasuso.pdf' target={'_blank'}>Acuerdo de Uso</a> de la Plataforma KOYN que incluye la Plitica de Privacidad aplicable al tratamiento de la informacin de datos personales.
                                        </Checkbox>
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Form.Item>
                                <Button type='primary' htmlType='submit' loading={isLoading} disabled={isLoading} block>Registrar</Button>
                            </Form.Item>
                        </Form>
                }
                {
                    error && <AlertWait error={error} isLoading={isLoading} />
                }
                <Row justify='center' style={{ paddingTop: '15px', marginBottom: '25px' }}>
                    <Col>
                        Ya ests registrado? <Button type='link' onClick={login}>Ingresa aqu</Button>
                    </Col>
                </Row>
            </Col>
        </Row >
    )
}

export default Register