import React from 'react'
import { Row, Col, Space, Alert, Spin, Typography } from 'antd'
import PropTypes from 'prop-types'

const AlertWait = (props) => {
    const { isLoading, error, mensaje } = props
    return (
        <Row justify='center'>
            <Col>
                <Space direction='vertical'
                    style={{
                        display: 'flex',
                    }}
                    size='large'
                >
                    {
                        error &&
                        <Alert message={error} type='warning' showIcon />
                    }
                    {
                        isLoading &&
                        <Row justify='center'>
                            <Col span={24} style={{ textAlign: 'center', padding: '20px 20px', display: 'grid' }}>
                                <Spin size='large'></Spin>
                                <Typography.Text strong>
                                    {
                                        mensaje
                                    }
                                </Typography.Text>
                            </Col>
                        </Row>
                    }
                </Space>
            </Col>
        </Row>
    )
}

AlertWait.propTypes = {
    isLoading: PropTypes.bool.isRequired,
    error: PropTypes.any,
    mensaje: PropTypes.node,
}

export default AlertWait