import axios from 'axios'
import AxiosInstance from "./../utils/AxiosInstance"
import { getOfferInvestmentListUrl, getOfferInvestmentByIdUrl } from "../utils/urls"
const config = {
    headers: { 'Accept-Language': 'en_US' }
}
export default class OfertaInversionService {
    gets = () => {
        return axios.create(config).get(getOfferInvestmentListUrl())
    }
    get = (props) => {
        const { navigate, ofertaInversion } = props
        return AxiosInstance(navigate).post(getOfferInvestmentByIdUrl(), ofertaInversion)
    }
}