import React from 'react'
import PropTypes from 'prop-types'
import { Typography } from 'antd'
import * as anticons from '@ant-design/icons'
import { formatoMoneda } from '../../utils/Utils'

const ResumenInversion = (props) => {
    const { cuota } = props
    return (
        <Typography.Title level={4} style={{ color: '#0E61D1' }}>
            <anticons.MoneyCollectOutlined /> Inversin total: {formatoMoneda(cuota || 0)}
        </Typography.Title>
    )
}

ResumenInversion.propTypes = {
    cuota: PropTypes.number.isRequired,
}

export default ResumenInversion