import React, { useState } from "react"
import { Link } from "react-router-dom"
import { Input, Form, Button, Row, Col, Divider } from 'antd'
import AlertWait from "./AlertWait"
import Logo from "../components/Logo"
import CuentaService from "../service/CuentaService"

const cuentaService = new CuentaService()

const RecuperarPassword = () => {
    const [isLoading, setIsLoading] = useState(false)
    const [error, setError] = useState("")
    const [form] = Form.useForm();
    const recuperar = (values) => {
        setError("")
        setIsLoading(true)
        cuentaService
            .recoverPassword({ datos: { ...values } })
            .then((response) => {
                setIsLoading(false)
                setError('Su contrasea fue enviada el correo indicado. Revise su badeja de entrada')
            })
            .catch((error) => {
                setIsLoading(false)
                const { response } = error
                if (response.status === 500) {
                    setError(response.statusText)
                } else if (response.status === 404) {
                    setError(
                        "El recurso solicitado no esta disponible o no se encuentra"
                    )
                } else {
                    setError(response.data[0].messageText)
                }
            })
    }

    return (
        <Row justify='center' align='middle' style={{ height: '100vh' }}>
            <Col xs={22} sm={12} md={12} lg={12} xl={6}>
                <Logo />
                <Divider><span style={{ textTransform: 'uppercase', letterSpacing: '1px' }}>Recuperar Contrasea</span></Divider>
                <Row>
                    <Col span={24}>
                        <Form
                            layout={'vertical'}
                            form={form}
                            onFinish={recuperar}
                        >
                            <Form.Item label="Email"
                                name='email'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese el Email',
                                    },
                                ]}
                            >
                                <Input id="email" name="email" label='Email' type='text' />
                            </Form.Item>
                            <Form.Item>
                                <Button type='primary' htmlType='submit' loading={isLoading} disabled={isLoading} block>Recuperar Contrasea</Button>
                            </Form.Item>
                        </Form>
                    </Col>
                </Row>
                <AlertWait error={error} isLoading={isLoading} />
                <Row style={{ paddingTop: '15px' }}>
                    <Col span={12}>
                        <Row justify='start'>
                            <Link to={'/login'}>Ingresa aqu</Link>
                        </Row>
                    </Col>
                    <Col span={12}>
                        <Row justify='end'>
                            <Link to={'/register'}>Regstrate aqu</Link>
                        </Row>
                    </Col>
                </Row>
            </Col>
        </Row>
    )
}

export default RecuperarPassword