import { getInsertInvestmentApplicationUrl, getInvestmentApplicationByFilterUrl, getInvestmentApplicationByIdUrl, getInvestmentApplicationListUrl } from "../utils/urls"
import AxiosInstance from "./../utils/AxiosInstance"

export default class SolicitudInversionService {
    gets = (props) => {
        const { navigate } = props
        return AxiosInstance(navigate).get(getInvestmentApplicationListUrl())
    }
    getsFilter = (props) => {
        const { navigate, solicitudInversion } = props
        return AxiosInstance(navigate).post(getInvestmentApplicationByFilterUrl(), solicitudInversion)
    }
    get = (props) => {
        const { navigate, solicitudInversion } = props
        return AxiosInstance(navigate).post(getInvestmentApplicationByIdUrl(), solicitudInversion)
    }
    put = (props) => {
        const { navigate, solicitudInversion } = props
        return AxiosInstance(navigate).put(getInsertInvestmentApplicationUrl(), solicitudInversion)
    }
}