import React, { useState, useEffect } from 'react'
import { useHistory, Link } from 'react-router-dom'
import { Typography, Button, Result, Table, Row, Col, Divider, Input, Radio, Form } from 'antd'
import * as anticons from '@ant-design/icons'
import UsuarioService from '../../service/UsuarioService'

const usuarioService = new UsuarioService()

const Usuarios = () => {

  const navigate = useHistory()
  const [isLoading, setIsLoading] = useState(true)
  const [usuarios, setUsuarios] = useState([])
  const [error, setError] = useState(null)

  const [documento, setDocumento] = useState(null)
  const [email, setEmail] = useState(null)
  const [nombres, setNombres] = useState(null)
  const [estado, setEstado] = useState(null)

  const tableProps = {
    bordered: false,
    loading: false,
    title: '',
    showHeader: true,
    size: 'medium',
    pagination: {
      position: ['none', 'bottomRight']
    }
  };

  const columnas = [
    {
      title: 'Nombre',
      dataIndex: 'unique',
      render: (e) => <Link to={`/usuario/${e.key}`}>{e.label}</Link>
    },
    {
      title: 'Identificacin',
      dataIndex: 'documento'
    },
    {
      title: 'Email',
      dataIndex: 'email',
      responsive: ['xl']
    },
    {
      title: 'Telfono',
      dataIndex: 'telefono',
      responsive: ['xl']
    },
    // {
    //   title: 'Tipo',
    //   dataIndex: 'perId',
    //   render: (text) => text.perNombre
    // },
    // {
    //   title: 'Estado',
    //   dataIndex: 'estado',
    //   render: (text) => text.catNombre
    // }
  ]

  useEffect(() => {
    if (isLoading) {
      const fetchData = async () => {
        try {
          let filtros = {
          }
          if (documento) {
            filtros = { ...filtros, documento }
          }
          if (email) {
            filtros = { ...filtros, email }
          }
          if (nombres) {
            filtros = { ...filtros, nombres }
          }
          if (estado !== null) {
            filtros = { ...filtros, estado }
          }
          const { data } = await usuarioService.getsFiltro({ navigate, filtros })
          if (data) {
            const _usuarios = data.listObject[0].map(u => ({ ...u, unique: { key: u.usuId, label: `${u.nombres} ${u.apellidos}` } }))
            setUsuarios(_usuarios)
            setError(null)
            setIsLoading(false)
          } else {
            setIsLoading(false)
            setError("Hubo un error al obtener los usuarios")
          }
        } catch (error) {
          setIsLoading(false)
          setError("No pudimos hacer la solicitud para obtener los usuarios")
        }
      }
      fetchData()
    }
  }, [isLoading, navigate, documento, email, nombres, estado])

  const fetchUsuarios = () => {
    setIsLoading(true)
  }

  if (error) {
    return (
      <Result
        status="warning"
        title={''}
        subTitle={''}
        extra={
          <>
            <h5>{error}</h5>
            <Link to={'/'}>Regresar al inicio</Link>
          </>
        }
      />
    )
  }

  return (
    <>
      <Row>
        <Col span={24}>
          <Row justify='start'>
            <Typography.Title
              level={4}
              style={{
                margin: 0,
              }}
            >
              Gestin de Usuarios
            </Typography.Title>
          </Row>
        </Col>
      </Row>
      <Divider></Divider>
      <Form
        layout={'vertical'}
      >
        <Row gutter={10}>
          <Col xs={24} sm={12} md={12} lg={6} xl={6}>
            <Form.Item label="Identificacin">
              <Input label='Identificacin' type='text' value={documento} onChange={(e) => setDocumento(e.target.value)} />
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={12} lg={6} xl={6}>
            <Form.Item label="Nombres">
              <Input label='Nombres' type='text' value={nombres} onChange={(e) => setNombres(e.target.value)} />
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={12} lg={6} xl={6}>
            <Form.Item label="Email">
              <Input label='Email' type='text' value={email} onChange={(e) => setEmail(e.target.value)} />
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={12} lg={6} xl={6}>
            <Form.Item label="Estado">
              <Radio.Group onChange={(e) => setEstado(e.target.value)} value={estado}>
                <Radio value={true}>Activo</Radio>
                <Radio value={false}>Inactivo</Radio>
              </Radio.Group>
            </Form.Item>
          </Col>
        </Row>
        <Row justify='center'>
          <Col>
            <Form.Item>
              <Button type="primary" onClick={fetchUsuarios} icon={<anticons.SearchOutlined />} loading={isLoading} disabled={isLoading}>Consultar</Button>
            </Form.Item>
          </Col>
        </Row>
      </Form>
      <Table
        {...tableProps}
        rowKey='usuId'
        columns={columnas}
        dataSource={usuarios}
        loading={isLoading}
      />
    </>
  )
}

export default Usuarios