import React, { useState } from 'react'
import { useHistory } from 'react-router-dom'
import { Input, Form, Button, Row, Col, Divider } from 'antd'
import PasswordChecklist from 'react-password-checklist'
import Titulo from './../Titulo'
import AlertWait from '../../pages/AlertWait'
import md5 from 'md5'
import CuentaService from './../../service/CuentaService'

const cuentaService = new CuentaService()

const ChangePassword = () => {

    const navigate = useHistory()
    const [isLoading, setIsLoading] = useState(false)
    const [error, setError] = useState("")
    const [form] = Form.useForm();

    const [newPassword, setNewPassword] = useState("")
    const [newPasswordConfirm, setNewPasswordConfirm] = useState("")

    const change = async values => {
        setError("")
        setIsLoading(true)
        try {
            const { data } = await cuentaService.changePassword({ navigate, datos: { password: md5(values.password), newPassword: md5(values.newPassword), newPasswordConfirm: md5(values.newPasswordConfirm) } })
            setIsLoading(false)
            setError('Su contrasea fue modificada. Ingrese nuevamente.')
            console.log(data)
        }
        catch (error) {
            setIsLoading(false)
            const { response } = error
            if (response.status === 500) {
                setError(response.statusText)
            } else if (response.status === 404) {
                setError(
                    "El recurso solicitado no esta disponible o no existe"
                )
            } else {
                setError(response.data[0].messageText)
            }
        }
    }

    return (
        <>
            <Titulo titulo="Cambiar Contrasea" />
            <Divider></Divider>
            <Row justify='center' align='middle'>
                <Col xs={24} sm={12} md={12} lg={12} xl={6}>
                    <Row>
                        <Col span={24}>
                            <Form
                                layout={'vertical'}
                                form={form}
                                onFinish={change}
                            >
                                <Form.Item label="Actual Contrasea"
                                    name='password'
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Ingrese la actual contrasea',
                                        },
                                    ]}
                                >
                                    <Input.Password autoComplete='' />
                                </Form.Item>
                                <Form.Item label="Nueva Contrasea"
                                    name='newPassword'
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Ingrese la nueva contrasea',
                                        },
                                    ]}
                                >
                                    <Input.Password autoComplete=''onChange={e => setNewPassword(e.target.value)} />
                                </Form.Item>
                                <Form.Item label="Confirme la Nueva Contrasea"
                                    name='newPasswordConfirm'
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Confirme la nueva contrasea',
                                        },
                                    ]}
                                >
                                    <Input.Password autoComplete='' onChange={e => setNewPasswordConfirm(e.target.value)} />
                                </Form.Item>
                                <Form.Item>
                                    <PasswordChecklist
                                        rules={["minLength", "specialChar", "number", "capital", "match"]}
                                        minLength={8}
                                        value={newPassword}
                                        valueAgain={newPasswordConfirm}
                                        messages={{
                                            minLength: "La contrasea tiene ms de 8 caracteres.",
                                            specialChar: "La contrasea tiene caracteres especiales.",
                                            number: "La contrasea tiene un nmero.",
                                            capital: "La contrasea tiene una letra mayscula.",
                                            match: "Las contraseas coinciden.",
                                        }}
                                        onChange={(isValid) => { }}
                                    />
                                </Form.Item>
                                <Form.Item>
                                    <Button type='primary' htmlType='submit' loading={isLoading} disabled={isLoading} block>Cambiar Contrasea</Button>
                                </Form.Item>
                            </Form>
                        </Col>
                    </Row>
                    <AlertWait error={error} isLoading={isLoading} />
                </Col>
            </Row>
        </>
    )
}

export default ChangePassword