import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { Image, Progress, Button, Spin, Card, Row, Col, message, Alert, Typography, Badge, Tooltip, Statistic, Avatar } from 'antd'
import * as anticons from '@ant-design/icons'
import { formatoMoneda, getEstiloFerificada, getURLImagenPYMERandom, getURLImagenRandom } from './../../utils/Utils'
import OfertaInversionService from '../../service/OfertaInversionService';

const ofertaInversionService = new OfertaInversionService()
const { Meta } = Card

const Inversiones = () => {

  const history = useHistory()
  const [inversiones, setInversiones] = useState([])
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState(null)

  const OpenInversion = (c) => {
    history.push(`/inversion/${c}`)
  }

  const VerInversion = (i) => {
    message.info('Abrir detalles de inversin')
  }

  const fetchData = async () => {
    try {
      const { data } = await ofertaInversionService.gets()
      if (data) {
        setIsLoading(false)
        setError(null)
        setInversiones(data.listObject)
      } else {
        setIsLoading(false)
        setError("Hubo un error al obtener las ofertas de inversin")
      }
    } catch (error) {
      setIsLoading(false)
      setError("No pudimos hacer la solicitud para obtener las ofertas de inversin")
    }
  }

  useEffect(() => {
    if (isLoading) {
      fetchData()
    }
  })

  const renderInversion = (i) => {
    const { oinCapital, oinDetalle, oinInversionistas, oinId, oinPersona, oinPlazo, oinRecaudado, oinTir } = i
    return (
      <Col key={oinId} xs={20} sm={10} md={10} lg={10} xl={7} xxl={5}>
        <Badge.Ribbon text={
          <span {...getEstiloFerificada()}>
            Verificada <anticons.CheckCircleOutlined></anticons.CheckCircleOutlined>
          </span>
        }>
          <Card
            bodyStyle={{ background: '#fff' }}
            hoverable={true}
            cover={
              <Image src={getURLImagenPYMERandom(oinId)} alt="" width={'100%'} heigh={'100%'} preview={false} />
            }
            actions={[]}
          >
            <Meta
              avatar={
                <Avatar src={getURLImagenRandom(oinId)} />
              }
              title={`${oinPersona.usuId.nombres !== undefined ? oinPersona.usuId.nombres : ''} ${oinPersona.usuId.apellidos !== undefined ? oinPersona.usuId.apellidos : ''}`}
              description={oinDetalle}
              style={{ paddingBottom: '15px' }}
            />
            {/* <Row>
              <Col span={24}>
                <Typography.Title level={5}>
                  Datos Inversin {oinIdPersona}
                </Typography.Title>
                <Typography.Text>
                  <p>
                    {oinDetalle}
                  </p>
                </Typography.Text>
              </Col>
            </Row> */}
            <Row>
              <Col span={12}>
                <Statistic
                  title={
                    'Recaudado'
                  }
                  value={
                    oinRecaudado && oinCapital ?
                      ((oinRecaudado || 0) * 100 / (oinCapital || 0)).toFixed(2) : 0
                  }
                  valueStyle={{
                    color: '#000',
                    fontSize: '1em'
                  }}
                  suffix="%"
                />
              </Col>
              <Col span={12}>
                <Row justify='end'>
                  <Statistic
                    title={
                      'Objetivo'
                    }
                    value={formatoMoneda(oinCapital || 0)}
                    valueStyle={{
                      color: '#000',
                      fontSize: '1em'
                    }}
                  />
                </Row>
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <Progress
                  strokeLinecap='square'
                  strokeColor={{
                    '0%': '#8A05BE',
                    '100%': '#006e2c',
                  }}
                  percent={((oinRecaudado || 0) * 100 / (oinCapital || 0)).toFixed(2)}
                  showInfo={false} trailColor={'#ccc'} size='small' />
              </Col>
            </Row>
            <Row style={{ textAlign: 'center', paddingTop: '15px' }}>
              <Col span={8} style={{ borderRight: 'solid 1px #f0f0f0' }}>
                <Tooltip title="Plazo">
                  <div>
                    <anticons.CalendarOutlined />
                    <br />
                    {oinPlazo} Meses
                  </div>
                </Tooltip>
              </Col>
              <Col span={8} style={{ borderRight: 'solid 1px #f0f0f0' }}>
                <Tooltip title="TIR">
                  <div>
                    <anticons.PercentageOutlined />
                    <br />
                    {oinTir}
                  </div>
                </Tooltip>
              </Col>
              <Col span={8}>
                <Tooltip title="Inversionistas">
                  <div>
                    <anticons.UserOutlined />
                    <br />
                    {oinInversionistas}
                  </div>
                </Tooltip>
              </Col>
            </Row>
            <Row style={{ textAlign: 'center', paddingTop: '15px' }}>
              <Col span={12}>
                <Button type='primary' onClick={() => VerInversion(i)} ghost>Ver Ms</Button>
              </Col>
              <Col span={12}>
                <Button type='primary' onClick={() => OpenInversion(i.oinId)}>Invertir</Button>
              </Col>
            </Row>
          </Card>
        </Badge.Ribbon>
      </Col>
    )
  }

  return (
    <>
      {
        isLoading &&
        <Row>
          <Col span={24}>
            <Row justify='center'>
              <Spin size="large" />
            </Row>
          </Col>
        </Row>
      }
      {
        error &&
        <Row>
          <Col span={24} style={{ padding: '0 0 15px 0' }}>
            <Alert description={error} type='warning' showIcon banner></Alert>
          </Col>
        </Row>
      }
      <Row>
        <Col span={24}>
          <Typography.Paragraph>
            <Typography.Title level={4}>
              Inversiones
            </Typography.Title>
            Existe una variedad de empresas en las que puede invertir en forma de crdito directo.
          </Typography.Paragraph>
        </Col>
      </Row>
      <Row justify='center' gutter={[20, 30]}>
        {
          inversiones.length > 0 && inversiones.map(i => renderInversion(i))
        }
      </Row>
    </>
  )
}

const comparisonFn = function (prevProps, nextProps) {
  return (prevProps.location.pathname === nextProps.location.pathname) && (prevProps.colorMode === nextProps.colorMode);
}

export default React.memo(Inversiones, comparisonFn)