import AxiosInstance from "../utils/AxiosInstance";
import { getInversionistaPorIdUrl, getUpdateInvestorUrl } from "../utils/urls";

export default class InversionistaService {
    get = (props) => {
        const { navigate, inversionista } = props
        return AxiosInstance(navigate).post(getInversionistaPorIdUrl(), inversionista)
    }
    set = (props) => {
        const { navigate, inversionista } = props
        return AxiosInstance(navigate).post(getUpdateInvestorUrl(), inversionista)
    }
}