import React, { useState } from "react"
import { Link } from "react-router-dom"
import { Input, Form, Button, Row, Col, Divider } from 'antd'
import PasswordChecklist from 'react-password-checklist'
import AlertWait from "./AlertWait"
import Logo from "../components/Logo"
import md5 from 'md5'
import CuentaService from "../service/CuentaService"

const cuentaService = new CuentaService()

const ResetPassword = () => {
    const [isLoading, setIsLoading] = useState(false)
    const [error, setError] = useState("")
    const [form] = Form.useForm()

    const [password, setPassword] = useState("")
    const [passwordConfirm, setPasswordConfirm] = useState("")

    const reset = (values) => {
        setError("")
        setIsLoading(true)
        cuentaService
            .resetPassword({ datos: { password: md5(values.password), passwordConfirm: md5(values.passwordConfirm) } })
            .then((response) => {
                setIsLoading(false)
                setError('Su contrasea fue modificada. Ingrese nuevamente.')
            })
            .catch((error) => {
                setIsLoading(false)
                const { response } = error
                if (response.status === 500) {
                    setError(response.statusText)
                } else if (response.status === 404) {
                    setError(
                        "El recurso solicitado no esta disponible o no se encuentra"
                    )
                } else {
                    setError(response.data[0].messageText)
                }
            })
    }

    return (
        <Row justify='center' align='middle' style={{ height: '100vh' }}>
            <Col xs={22} sm={12} md={12} lg={12} xl={6}>
                <Logo />
                <Divider><span style={{ textTransform: 'uppercase', letterSpacing: '1px' }}>Cambiar Contrasea</span></Divider>
                <Row>
                    <Col span={24}>
                        <Form
                            layout={'vertical'}
                            form={form}
                            onFinish={reset}
                        >
                            <Form.Item label="Nueva Contrasea"
                                name='password'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese la nueva contrasea',
                                    },
                                ]}
                            >
                                <Input.Password label='Nueva Contrasea' onChange={e => setPassword(e.target.value)} />
                            </Form.Item>
                            <Form.Item label="Confirme la Nueva Contrasea"
                                name='passwordConfirm'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Confirme la nueva contrasea',
                                    },
                                ]}
                            >
                                <Input.Password label='Confirme la Nueva Contrasea' onChange={e => setPasswordConfirm(e.target.value)} />
                            </Form.Item>
                            <Form.Item>
                                <PasswordChecklist
                                    rules={["minLength", "specialChar", "number", "capital", "match"]}
                                    minLength={8}
                                    value={password}
                                    valueAgain={passwordConfirm}
                                    messages={{
                                        minLength: "La contrasea tiene ms de 8 caracteres.",
                                        specialChar: "La contrasea tiene caracteres especiales.",
                                        number: "La contrasea tiene un nmero.",
                                        capital: "La contrasea tiene una letra mayscula.",
                                        match: "Las contraseas coinciden.",
                                    }}
                                    onChange={(isValid) => { }}
                                />
                            </Form.Item>
                            <Form.Item>
                                <Button type='primary' htmlType='submit' loading={isLoading} disabled={isLoading} block>Cambiar Contrasea</Button>
                            </Form.Item>
                        </Form>
                    </Col>
                </Row>
                <AlertWait error={error} isLoading={isLoading} />
                <Row style={{ paddingTop: '15px' }}>
                    <Col span={12}>
                        <Row justify='start'>
                            <Link to={'/login'}>Ingresa aqu</Link>
                        </Row>
                    </Col>
                    <Col span={12}>
                        <Row justify='end'>
                            <Link to={'/register'}>Regstrate aqu</Link>
                        </Row>
                    </Col>
                </Row>
            </Col>
        </Row>
    )

}

export default ResetPassword